<template>
  <div class="container">
    <div class="header">
      <el-radio-group v-model="queryInfo.area" @change="handleClick">
        <el-radio-button label="ALL">全部</el-radio-button>
        <el-radio-button label="ZH">华语</el-radio-button>
        <el-radio-button label="EA">欧美</el-radio-button>
        <el-radio-button label="JP">日本</el-radio-button>
        <el-radio-button label="KR">韩国</el-radio-button>
      </el-radio-group>
    </div>
    <hr />
    <AlbumsList :albums="albums"></AlbumsList>
    <div class="footer">
      <el-pagination
        background
        layout="prev, pager, next"
        :total="total"
        :page-size="queryInfo.limit"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
import AlbumsList from "@/components/AlbumsList.vue";
export default {
  components: {
    AlbumsList,
  },
  data() {
    return {
      queryInfo: {
        area: "ALL",
        limit: 30,
        offset: 0,
      },
      albums: [],
      total: 0,
      currentPage: 1,
    };
  },
  created() {
    this.getAlbums();
  },
  methods: {
    async getAlbums() {
      const { data: res } = await this.$http.get("/album/new", {
        params: this.queryInfo,
      });
      // console.log(res);
      this.albums = res.albums;
      this.total = res.total;
    },
    handleClick() {
      this.currentPage = 1;
      this.queryInfo.offset = 0;
      this.getAlbums();
    },
    handleCurrentChange() {
      this.queryInfo.offset = (this.currentPage - 1) * this.queryInfo.limit;
      this.getAlbums();
    },
  },
};
</script>

<style lang="less" scoped>
.container {
  padding: 0 50px;
  .header {
    padding: 20px;
  }
  hr {
    border: 1px solid #303133;
    margin-bottom: 20px;
  }
  .footer {
    display: flex;
    justify-content: center;
  }
}
</style>